section{
    padding:2rem 9%;
}

.home{
    display: flex;
     align-items: center;
    justify-content: center;
    background: url(../assets/banner-bg.webp) no-repeat;
    background-position: center;
    background-size: cover;
    padding-top: 17rem;
    padding-bottom: 10rem; 
}

.home .content{
    text-align: center;
    width:60rem;
}

.home .content h3{
    color: var(--black);
    font-size: 3rem;
}

.home .content h3 span{
    color: var(--orange);
}

.home .content p{
    color: var(--light-color);
    font-size: 1.7rem;
    padding: 1rem 0;
    line-height: 1.8;
}






@media  (max-width:991px){
    section{
        padding:2rem;
    }
}